.Row {
    height: calc(100vh - 81px);
  }
  .Input {
    font-family: sans-serif;
    background: #f0f0f0;
    border: 3px solid #6f00ff;
    box-sizing: border-box;
    border-radius: 6px;
    color: #000000;
    text-align: center;
    padding: 10px;
  }
  
  .Header {
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 84px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #8c5af1;
  }
  

  .Link {
    text-decoration: none;
    font-size: 20px;
    text-align: center;
    display: block;
    margin: auto;
  }
  
  .Link span {
    color: #9431ff;
  }
  
  .Container {
    padding: 30px 10px;
  }
  
  .SignInCol {
    height: 320px;
  }
  
  .SignUpCol {
    height: 375px;
  }