.Button {
    font-size: 1rem;
    align-self: center;
    border-radius: 25px;
    border-color: transparent;
    padding: 4px 10px;
    margin-left: 2px;
    margin-right: 2px;
    min-width: 75px;
  }
  
  .Button:hover {
    opacity: 0.8;
    border-color: transparent;
    cursor: pointer;
  }
  
  .Button:hover:active {
    background-color: #8548e2 !important;
  }
  
  .Button:active {
    background-color: #8548e2 !important;
  }
  
  .Button:focus {
    background-color: #8548e2;
  }
  
  .Wide {
    padding: 10px 50px;
    width: 100%;
  }
  
  .Blue,
  .BlueOutline:hover,
  .Bright:hover {
    background-color:#8548e2;
    color: #ffffff;
  }
  
  .BlueOutline,
  .Blue:hover {
    background-color: #ffffff;
    color: #8921b2;
  }
  
  .Black,
  .BlackOutline:hover {
    background-color: #bb21f8;
    color: aliceblue;
  }
  
  .Black:hover,
  .BlackOutline {
    background-color: aliceblue;
    color: #9857de;
  }
  
  .Bright {
    background-color:#ae62f1; 
    color: #ffffff;
  }