.SearchBar input {
    border-radius: 20px;
    color: #242a3d;
    padding-left: 40px;
    margin-bottom: 16px;
  }
  
  .Search {
    position: absolute;
    font-size: 1.2rem;
    padding: 9px;
    padding-left: 15px;
    color: rgb(255, 55, 55);
  }
  
  .SearchIcon:hover {
    cursor: default;
    color: #cfced3;
  }

  .ChatFormContainer {
  position: fixed;   /* Position fixed so it stays in place */
  bottom: 0;          /* Align to the bottom of the viewport or container */
  width: 600px;        /* Occupy full width */
  background-color: #ffffff; /* Optionally, set a background color */
  padding: 15px;      /* Adjust padding as needed */
  box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.1); 
  }